"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { useSession } from "next-auth/react";
import { v4 as uuidv4 } from "uuid";
import html2pdf from "html2pdf.js";

type EducationModel = {
  id: string;
  name: string;
  philosophy: string;
  evaluationFocus: string;
  languageFocus: string;
  childFocus: string;
  updatedAt: string;
};

export default function ModelListPage() {
  const { data: session } = useSession();
  const [models, setModels] = useState<EducationModel[]>([]);
  const [editId, setEditId] = useState<string | null>(null);
  const [form, setForm] = useState({
    name: "",
    philosophy: "",
    evaluationFocus: "",
    languageFocus: "",
    childFocus: "",
  });
  const [error, setError] = useState("");

  // localStorageから読み込み
  useEffect(() => {
    const stored = localStorage.getItem("styleModels");
    if (stored) setModels(JSON.parse(stored));
  }, []);

  const handleChange = (field: keyof typeof form, value: string) => {
    setForm((prev) => ({ ...prev, [field]: value }));
  };

  const startEdit = (m: EducationModel) => {
    setEditId(m.id);
    setForm({
      name: m.name,
      philosophy: m.philosophy,
      evaluationFocus: m.evaluationFocus,
      languageFocus: m.languageFocus,
      childFocus: m.childFocus,
    });
  };

  const cancelEdit = () => {
    setEditId(null);
    setForm({
      name: "",
      philosophy: "",
      evaluationFocus: "",
      languageFocus: "",
      childFocus: "",
    });
    setError("");
  };

  const saveEdit = () => {
    if (
      !form.name.trim() ||
      !form.philosophy.trim() ||
      !form.evaluationFocus.trim() ||
      !form.languageFocus.trim() ||
      !form.childFocus.trim()
    ) {
      setError("必須項目をすべて入力してください。");
      return;
    }
    const now = new Date().toISOString();

    let updatedModels: EducationModel[];
    if (editId) {
      updatedModels = models.map((m) =>
        m.id === editId ? { id: editId, ...form, updatedAt: now } : m
      );
    } else {
      // ここは編集専用なので基本使わないはずですが新規もOK
      updatedModels = [
        { id: uuidv4(), ...form, updatedAt: now },
        ...models,
      ];
    }

    setModels(updatedModels);
    localStorage.setItem("styleModels", JSON.stringify(updatedModels));
    cancelEdit();
  };

  const handleDelete = (id: string) => {
    if (!confirm("このモデルを削除しますか？")) return;
    const filtered = models.filter((m) => m.id !== id);
    setModels(filtered);
    localStorage.setItem("styleModels", JSON.stringify(filtered));
  };

  // PDF生成
  const handlePdf = async (model: EducationModel) => {
    const tempDiv = document.createElement("div");
    tempDiv.style.padding = "20px";
    tempDiv.style.fontFamily = "sans-serif";
    tempDiv.style.backgroundColor = "#fff";
    tempDiv.style.color = "#000";
    tempDiv.innerHTML = `
      <h1>${model.name}</h1>
      <h2>教育観</h2><p>${model.philosophy.replace(/\n/g, "<br>")}</p>
      <h2>評価観点の重視点</h2><p>${model.evaluationFocus.replace(/\n/g, "<br>")}</p>
      <h2>言語活動の重視点</h2><p>${model.languageFocus.replace(/\n/g, "<br>")}</p>
      <h2>育てたい子どもの姿</h2><p>${model.childFocus.replace(/\n/g, "<br>")}</p>
      <p>更新日時: ${model.updatedAt}</p>
    `;
    document.body.appendChild(tempDiv);
    try {
      await html2pdf()
        .from(tempDiv)
        .set({
          margin: 10,
          jsPDF: { unit: "mm", format: "a4", orientation: "portrait" },
          html2canvas: { scale: 2 },
          pagebreak: { mode: ["avoid-all"] },
        })
        .save(`${model.name}_教育観モデル.pdf`);
    } finally {
      document.body.removeChild(tempDiv);
    }
  };

  // Google Driveアップロード
  const handleDriveSave = async (model: EducationModel) => {
    if (!session || !("accessToken" in session)) {
      alert("Googleアカウントでログインしてください。");
      return;
    }
    const accessToken = (session as any).accessToken;
    const tempDiv = document.createElement("div");
    tempDiv.style.padding = "20px";
    tempDiv.style.fontFamily = "sans-serif";
    tempDiv.style.backgroundColor = "#fff";
    tempDiv.style.color = "#000";
    tempDiv.innerHTML = `
      <h1>${model.name}</h1>
      <h2>教育観</h2><p>${model.philosophy.replace(/\n/g, "<br>")}</p>
      <h2>評価観点の重視点</h2><p>${model.evaluationFocus.replace(/\n/g, "<br>")}</p>
      <h2>言語活動の重視点</h2><p>${model.languageFocus.replace(/\n/g, "<br>")}</p>
      <h2>育てたい子どもの姿</h2><p>${model.childFocus.replace(/\n/g, "<br>")}</p>
      <p>更新日時: ${model.updatedAt}</p>
    `;
    document.body.appendChild(tempDiv);
    try {
      const pdfBlob = await html2pdf()
        .from(tempDiv)
        .set({
          margin: 10,
          jsPDF: { unit: "mm", format: "a4", orientation: "portrait" },
          html2canvas: { scale: 2 },
          pagebreak: { mode: ["avoid-all"] },
        })
        .outputPdf("blob");

      const metadata = {
        name: `${model.name}_${new Date().toISOString()}.pdf`,
        mimeType: "application/pdf",
      };

      const formData = new FormData();
      formData.append(
        "metadata",
        new Blob([JSON.stringify(metadata)], { type: "application/json" })
      );
      formData.append("file", pdfBlob);

      const res = await fetch(
        "https://www.googleapis.com/upload/drive/v3/files?uploadType=multipart",
        {
          method: "POST",
          headers: { Authorization: `Bearer ${accessToken}` },
          body: formData,
        }
      );

      if (!res.ok) {
        const errorText = await res.text();
        throw new Error(`Google Driveアップロードに失敗しました: ${errorText}`);
      }
      alert("Google Driveに保存しました！");
    } catch (error) {
      alert(`エラーが発生しました: ${error}`);
    } finally {
      document.body.removeChild(tempDiv);
    }
  };

  return (
    <main style={{ padding: 24, fontFamily: "sans-serif", maxWidth: 900, margin: "0 auto" }}>
      <h1>教育観モデル一覧・編集</h1>

      {error && (
        <p style={{ color: "red", marginBottom: 12 }}>{error}</p>
      )}

      {editId && (
        <section style={{ border: "1px solid #ccc", padding: 16, marginBottom: 24, borderRadius: 8 }}>
          <h2>編集モード</h2>
          <input
            placeholder="モデル名"
            value={form.name}
            onChange={(e) => handleChange("name", e.target.value)}
            style={{ width: "100%", marginBottom: 8, padding: 8 }}
          />
          <textarea
            placeholder="教育観"
            rows={2}
            value={form.philosophy}
            onChange={(e) => handleChange("philosophy", e.target.value)}
            style={{ width: "100%", marginBottom: 8, padding: 8 }}
          />
          <textarea
            placeholder="評価観点の重視点"
            rows={2}
            value={form.evaluationFocus}
            onChange={(e) => handleChange("evaluationFocus", e.target.value)}
            style={{ width: "100%", marginBottom: 8, padding: 8 }}
          />
          <textarea
            placeholder="言語活動の重視点"
            rows={2}
            value={form.languageFocus}
            onChange={(e) => handleChange("languageFocus", e.target.value)}
            style={{ width: "100%", marginBottom: 8, padding: 8 }}
          />
          <textarea
            placeholder="育てたい子どもの姿"
            rows={2}
            value={form.childFocus}
            onChange={(e) => handleChange("childFocus", e.target.value)}
            style={{ width: "100%", marginBottom: 8, padding: 8 }}
          />
          <div>
            <button onClick={saveEdit} style={{ marginRight: 12 }}>
              保存
            </button>
            <button onClick={cancelEdit}>キャンセル</button>
          </div>
        </section>
      )}

      {models.length === 0 ? (
        <p>モデルがありません</p>
      ) : (
        models.map((m) => (
          <div
            key={m.id}
            style={{
              border: "1px solid #ccc",
              padding: 16,
              marginBottom: 12,
              borderRadius: 8,
              display: "flex",
              flexDirection: "column",
              gap: 8,
            }}
          >
            <h3 style={{ margin: 0 }}>{m.name}</h3>
            <p><strong>教育観：</strong> {m.philosophy}</p>
            <p><strong>評価観点：</strong> {m.evaluationFocus}</p>
            <p><strong>言語活動：</strong> {m.languageFocus}</p>
            <p><strong>育てたい子ども：</strong> {m.childFocus}</p>

            <div style={{ display: "flex", gap: 8, marginTop: 8 }}>
              <button onClick={() => startEdit(m)} style={buttonStyle}>
                編集
              </button>
              <button onClick={() => handleDelete(m.id)} style={{ ...buttonStyle, backgroundColor: "#f44336" }}>
                削除
              </button>
              <button onClick={() => handlePdf(m)} style={{ ...buttonStyle, backgroundColor: "#2196f3" }}>
                PDF化
              </button>
              <button onClick={() => handleDriveSave(m)} style={{ ...buttonStyle, backgroundColor: "#4285f4" }}>
                Drive保存
              </button>
            </div>
          </div>
        ))
      )}
    </main>
  );
}

const buttonStyle: React.CSSProperties = {
  padding: "8px 12px",
  border: "none",
  borderRadius: 6,
  cursor: "pointer",
  fontWeight: "bold",
  color: "white",
  backgroundColor: "#4caf50",
};
